<template>
    <div class="page-container">
        <div class="board-container">
            <div class="board-list">
                <div class="board-list-header">
                    <div class="up-down">
                        <!--v-btn class="white-button" @click="upDownBoard('up')" depressed>
                            <font-awesome-icon icon="fa-solid fa-angle-up"/>
                        </v-btn>
                        <v-btn class="white-button" @click="upDownBoard('down')" depressed>
                            <font-awesome-icon icon="fa-solid fa-angle-down"/>
                        </v-btn -->
                    </div>
                    <div class="add">
                        <v-btn class="white-button" @click="addBoard()" depressed>
                            <font-awesome-icon icon="fa-solid fa-plus"/>
                            게시판 추가
                        </v-btn>
                    </div>
                </div>
                <div class="board-list-body">
                    <ul v-for="(item,index) in boardList" class="main-board">
                        <li class="main-li">
                            <div :class="'main-data '+item.active">
                                <div class="gname" @click="choiceBoard(item.id)">
                                    {{ item.bname }}
                                    <font-awesome-icon v-if="item.buse=='yes'" icon="fa-solid fa-eye"/>
                                    <font-awesome-icon v-else icon="fa-solid fa-eye-slash"/>
                                    <font-awesome-icon v-if="item.impt=='yes'" style="color:#C51162;margin-left:7px;" icon="fa-solid fa-star" />
                                </div>

                                <div class="btn" @click="deleteBoard(item.id,index)" v-if="item.btype!='faq' && item.btype!='notice'">
                                    <font-awesome-icon icon="fa-solid fa-circle-minus"/>
                                </div>
                            </div>
                        </li>
                    </ul>
                </div>
            </div>
            <div class="board-regist">
                <div v-if="bcode">
                    <div class="board-title">{{ boardRegistTitle }}</div>
                    <form novalidate @submit.prevent="validate">
                        <div class="board-reg-box" style="display: flex;">
                            <ul class="table-ul">
                                <li>
                                    <div class="label-input">
                                        <div class="input-box">
                                            <v-text-field v-model="boardId"
                                                          dense
                                                          :error-messages="boardIdErrors"
                                                          label="게시판 아이디"
                                                          outlined
                                                          @input="$v.boardId.$touch()"
                                                          @blur="$v.boardId.$touch()"
                                                          :readonly="boardIdReadonly"/>
                                        </div>
                                    </div>
                                </li>
                                <li>
                                    <div class="label-input">
                                        <div class="input-box">
                                            <v-text-field v-model="bname"
                                                          :error-messages="bnameErrors"
                                                          dense
                                                          label="게시판명"
                                                          outlined
                                                          @input="$v.bname.$touch()"
                                                          @blur="$v.bname.$touch()"
                                                          placeholder="게시판명을 입력해주세요"/>
                                        </div>
                                    </div>
                                </li>
                                <li>
                                    <div class="label-input">
                                        <div class="input-box">
                                            <v-select v-model="wauth"
                                                      :items="authList"
                                                      dense
                                                      label="글쓰기 권한"
                                                      outlined/>
                                        </div>
                                    </div>
                                </li>
                                <li>
                                    <div class="label-input">
                                        <div class="input-box">
                                            <v-select v-model="categoryUse"
                                                      dense
                                                      :items="yesNoList"
                                                      label="카테고리 사용여부"
                                                      outlined/>
                                        </div>
                                    </div>
                                </li>
                                <li style="display: block;max-height:100px;">
                                    <div class="label-input">
                                        <div class="input-box">
                                            <v-select v-model="impt"
                                                      dense
                                                      :items="yesNoList"
                                                      label="주요게시판 사용여부"
                                                      outlined/>
                                        </div>

                                    </div>
                                    <div class="tip" style="margin-top:-20px;color:#F4364C">주요게시판은 통합검색에서 체크박스항목이며 통계시에는 기준이 되는 게시판입니다 </div>
                                </li>
                                <li>
                                    <div class="label">
                                        게시판 사용여부
                                    </div>
                                    <div class="label-input">
                                        <div class="radio-box">
                                            <v-radio-group
                                                v-model="buse"
                                                dense
                                                row
                                            >
                                                <v-radio
                                                    label="사용"
                                                    value="yes"
                                                ></v-radio>
                                                <v-radio
                                                    label="사용안함"
                                                    value="no"
                                                ></v-radio>
                                            </v-radio-group>
                                        </div>
                                    </div>
                                </li>
                            </ul>
                            <div v-if="categoryUse=='yes'" style="margin-left:50px;">
                                <div style="display: flex;justify-content: space-between">
                                    <div class="board-title" style=" font-size: 14px;padding-bottom:0;">카테고리 설정</div>
                                    <div class="add" style="margin:-5px 70px 0 0;">
                                        <v-btn class="white-button" @click="addCategory()" depressed>
                                            카테고리 추가
                                        </v-btn>
                                    </div>
                                </div>
                                <ul class="table-ul">
                                    <li v-for="(cate,index) in categoryList">
                                        <div class="label-input">
                                            <div class="input-box">
                                                <v-text-field v-model="categoryNames[index]"
                                                              :error-messages="categoryErrors[index]"
                                                              dense
                                                              outlined
                                                              :autofocus="categoryFocus[index]"
                                                              @input="categoryNameError(index)"
                                                              @blur="categoryNameError(index)"
                                                              label="카테고리명"
                                                              placeholder="카테고리명을 입력해주세요"/>
                                            </div>
                                        </div>
                                        <div class="label-input" style="padding-left:10px">
                                            <div class="radio-box">
                                                   <v-checkbox
                                                           v-model="cate.use"
                                                            label="사용"
                                                            value="yes"
                                                    ></v-checkbox>

                                            </div>
                                        </div>
                                        <div class="del" style="min-width:70px;margin-left:10px;">
                                            <v-btn v-if="index>0" class="delete-button" style="margin-top:13px;min-width:50px;max-width:50px;min-height:30px;max-height:30px;" @click="deleteCategory(index)" depressed>
                                                <span style="font-size:12px;">삭제</span>
                                            </v-btn>
                                        </div>
                                    </li>
                                </ul>
                            </div>
                        </div>
                        <div class="button-row">
                            <v-btn class="submit-button" type="submit">게시판정보 {{ buttonTitle }}</v-btn>
                        </div>
                    </form>
                </div>
            </div>
        </div>


    </div>
</template>


<script>
import apiService from '@/config/admin/apiService'
import {
    API_ADMIN_INSERT_BOARD,
    API_ADMIN_UPDATE_BOARD,
    API_ADMIN_GET_BOARD_LIST,
    API_ADMIN_DELETE_BOARD,
    API_ADMIN_SEQUENCE_BOARD
} from '@/config/admin/urls'
import {validationMixin} from 'vuelidate'
import {required, numeric} from 'vuelidate/lib/validators'

export default {
    mixins: [validationMixin],
    validations: {
        bname: {required},
        boardId:{required},
    },
    computed: {
        bnameErrors() {
            const errors = []
            if (!this.$v.bname.$dirty) return errors
            !this.$v.bname.required && errors.push('게시판명을 입력하세요')
            return errors;
        },
        boardIdErrors() {
            const errors = []
            if (!this.$v.boardId.$dirty) return errors
            !this.$v.boardId.required && errors.push('게시판 아이디를 입력하세요')
            return errors;
        },
    },
    watch: {
        categoryUse(val) {
            if (val == 'yes') {
                if (this.categoryList.length < 1) {
                    this.categoryNames.push('')
                    this.categoryErrors.push('')
                    let code = this.bcode + '_' + (this.categoryList.length + 1);
                    this.categoryList.push({code: code, name: '',use:'yes'});
                }

            }

        }
    },
    data() {
        return {
            boardList: [],
            boardRegistTitle: '게시판 상세설정',
            boardId: '',
            boardIdReadonly:false,
            bcode: '',
            bname: '',
            buse: 'yes',
            impt:'no',
            buttonTitle: '저장',
            serverFlag: false,
            wauth: 'all',
            authList: [
                {text: '모든 사용자', value: 'all'},
                {text: '회원만 가능', value: 'user'},
                {text: '관리자만 가능', value: 'admin'}
            ],
            categoryUse: 'no',
            yesNoList: [
                {text: '사용', value: 'yes'},
                {text: '사용안함', value: 'no'}
            ],
            categoryNames: [],
            categoryErrors: [],
            categoryList: [],
            categoryFocus:[],

        };
    },
    created() {
        this.getData();
    },
    methods: {
        getData() {
            if (this.serverFlag) return;
            this.serverFlag = true;
            apiService.post(API_ADMIN_GET_BOARD_LIST, {}, (res) => {
                this.serverFlag = false;
                this.boardList = [];
                for (let item of res.data) {
                    item.active = '';
                    this.boardList.push(item);
                }
                if (this.boardList[0]) this.choiceBoard(this.boardList[0].id)

            })
        },
        choiceBoard(id) {

            let activeInfo = {};
            for (let item of this.boardList) {
                if (item.id == id) {
                    item.active = 'active';
                    activeInfo = item;
                } else {
                    item.active = '';
                }
            }
            this.bcode = 'b'+activeInfo.id;
            this.boardId = activeInfo.bid;
            this.boardIdReadonly = true;
            this.bname = activeInfo.bname;
            this.buse = activeInfo.buse;
            this.categoryUse = activeInfo.categoryUse;
            if(this.categoryUse=='yes' && activeInfo.categoryList) {
                this.categoryNames = [];
                this.categoryErrors = [];
                this.categoryList = JSON.parse(activeInfo.categoryList);
                for(let key in this.categoryList) {
                    this.categoryNames.push(this.categoryList[key].name)
                    this.categoryErrors.push('')
                }


            } else {
                this.categoryNames = [];
                this.categoryErrors = [];
                this.categoryList = [];
            }

            this.wauth = activeInfo.wauth;
            this.impt = activeInfo.impt;
        },
        addBoard() {
            if (!this.boardIdReadonly && this.bcode) {
                return;
            }
            for (let item of this.boardList) {
                item.active = '';
            }
            const codeDate = new Date();
            this.bcode = codeDate.getTime();
            this.boardId = '';
            this.boardIdReadonly = false;
            this.bname = ''
            this.buse = 'yes';
            this.categoryUse = 'no';
            this.categoryNames = [];
            this.categoryErrors = [];
            this.categoryList = [];
            this.wauth = 'all';
            this.impt = 'no';

        },

        deleteBoard(id, index) {
            this.deleteConfirm(id, index);

        },
        deleteConfirm(id, index) {
            const confirm = {
                type: 'confirm',
                message: '선택된 게시판를 삭제하시겠습니까?',
                doAction: () => {
                    this.deleteAction(id, index);
                }
            }
            this.$eventBus.$emit('modalOpen', confirm);
        },
        deleteAction(id, index) {
            if (this.serverFlag) return;
            this.serverFlag = true;
            this.$eventBus.$emit('overlay', 'open');
            apiService.post(API_ADMIN_DELETE_BOARD, {id: id}, (res) => {
                this.serverFlag = false;
                this.$eventBus.$emit('overlay', 'hide');
                if (res.status == 'success') {
                    const params = {
                        message: '선택된 게시판이 삭제되었습니다',
                    }
                    this.$eventBus.$emit('modalOpen', params);
                    this.boardList.splice(index, 1)
                    if (this.boardId == id) {
                        if (this.boardList[0]) this.choiceBoard(this.boardList[0].id);
                    }
                } else {
                    const params = {
                        message: '선택된 게시판에 등록된 글이 1개 이상 있어서 삭제할수 없습니다',

                    }
                    this.$eventBus.$emit('modalOpen', params);
                }
            })

        },
        validate() {

            this.$v.$touch();
            if (!this.$v.$anyError) {
                if(this.categoryUse == 'yes') {
                    let index = 0;
                    let flag = false;
                    for(let cate of this.categoryList) {
                        this.categoryFocus[index] = false;
                        if (!this.categoryNames[index]) {
                            this.categoryNameError(index);
                            this.categoryFocus[index] = true;
                            flag = true;
                        } else {
                            cate.name = this.categoryNames[index];
                        }
                        index++;
                    }
                    if(flag)return;
                } else {
                    this.categoryList = [];
                }
                let frm = {
                    bid:this.boardId,
                    bname: this.bname,
                    buse: this.buse,
                    categoryUse: this.categoryUse,
                    categoryList: JSON.stringify(this.categoryList),
                    wauth: this.wauth,
                    impt:this.impt,
                }
                let url = API_ADMIN_INSERT_BOARD;
                if (this.boardIdReadonly) {
                    url = API_ADMIN_UPDATE_BOARD;
                }

                if (this.serverFlag) return;
                this.serverFlag = true;
                this.$eventBus.$emit('overlay', 'open');
                apiService.post(url, frm, (res) => {
                    this.serverFlag = false;
                    this.$eventBus.$emit('overlay', 'hide');
                    if(res.status=='success') {
                        let params = {};
                        if (res.data) {
                            if (this.boardIdReadonly) {
                                params = {
                                    message: '게시판정보가 수정 되었습니다'
                                }
                                let index = 0;
                                for(let item of this.boardList) {
                                    if(item.bid==this.boardId) {
                                        break;
                                    }
                                    index++;

                                };
                                this.$set(this.boardList,index,res.data)

                            } else {
                                params = {
                                    message: '게시판정보가 저장 되었습니다'
                                }
                                res.data.active = '';
                                this.boardList.push(res.data)
                            }
                        }
                        this.bcode = '';
                        this.boardId = '';
                        this.boardIdReadonly = false;
                        this.$eventBus.$emit('modalOpen', params);
                    } else {
                        if(res.data=='isBid') {
                            let params = {
                                message: '동일한 게시판 아이디가 존재합니다'
                            }
                            this.$eventBus.$emit('modalOpen', params);
                        }
                    }

                })
            }
        },
        updateSet(upd, result) {
            let j = 0;
            for (let item of this.boardList) {
                if (item.id == this.boardId) {
                    const mainItem = this.updateDataSet(item, upd, result)
                    this.$set(this.boardList, j, mainItem)
                    break;
                }
                j++;
            }
        },
        updateDataSet(item, upd, result) {

            if (upd) {
                item = result;
                item.active = 'active';

            } else {
                item = result;
                item.active = 'active';
                this.boardId = item.id;
            }
            return item;
        },
        upDownBoard(cmd) {
            if (!this.boardId) {
                return;
            }
            if (this.serverFlag) return;
            this.serverFlag = true;
            apiService.post(API_ADMIN_SEQUENCE_BOARD, {id: this.boardId, cmd: cmd}, (res) => {
                this.serverFlag = false;
                if (res.data) {
                    if (res.data == 'stay') return;
                    this.boardList = [];
                    for (let item of res.data) {
                        if (item.id == this.boardId) {
                            item.active = 'active';
                        } else item.active = '';
                        this.boardList.push(item);
                    }

                }
            })
        },
        addCategory() {
            if (this.categoryList.length > 7) {
                let params = {
                    message: '카테고리는 최대 8개까지 추가 가능합니다'
                }
                this.$eventBus.$emit('modalOpen', params);
                return;
            }
            this.categoryNames.push('')
            this.categoryErrors.push('')
            let code = this.bcode + '_' + (this.categoryList.length + 1);
            this.categoryList.push({code: code, name: '',use:'yes'});
        },
        categoryNameError(index) {
            if (!this.categoryNames[index]) {
                this.categoryErrors[index] = '카테고리명을 입력하세요'
            } else {
                this.categoryErrors[index] = ''
            }
        },
        deleteCategory(index) {
            this.categoryNames.splice(index,1)
            this.categoryErrors.splice(index,1)
            this.categoryList.splice(index,1)

        }
    }

}
</script>

